.SideDrawer{
    top:0;
    left: 0;
    position: fixed;
    width: 280px;
    max-width: 70%;
    height: 100%;
    z-index: 200;
    background-color: #e2cc69;
    padding: 32px 16px;
    box-sizing: border-box;
    transition: transform 0.3s ease-out;
    text-align: center;
 /* color: gold */
}

@media (min-width: 500px){
    .SideDrawer{
        display: none;
    }
}

.Open {
    transform: translateX(0);
}

.Close{
    transform: translateX(-100%);
}

.Logo{
    height: 11%;
    margin: 32px auto;
    width: 40%;
}